.discriptionBlok {
    display: grid;
    grid-template-columns: 220px 350px;
    grid-gap: 10px;
    background-color: white;
    /*padding: 10px;*/
}

.imgGrid {
    background-color: WhiteSmoke;
    height: 300px;
    border-radius: 5px;
}

.imgGrid img {
    width: 200px;
    height:240px;
    margin: 10px 10px 2px 10px;
    border-radius: 5px;
}

.buttonPhoto {
    background-color: white;
    width: 200px;
    height: 40px;
    margin: 0 10px 10px 10px;
    border-radius: 2px;
}
.buttonPhoto:hover {
    background-color: #c4c4c4;
}

.gridIn {
    background-color: WhiteSmoke;
    height:280px;
    padding: 10px;
    border-radius: 5px;
}


.statusSpan:hover {
    background-color: #d6d6d6;
}

.statusSpan {
    color: #a3a3a3;
}

.status input {
    width: 300px;
}

